.SoundBars {
  -ms-flex: 1 1;
      flex: 1 1;
  --columns: 4;
  position: relative;
}

.SoundBars-paused .SoundBars-bar {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

.SoundBars-bar {
  background: var(--primary);
  position: absolute;
  bottom: 0;
  top: 98%;
  width: calc((100% / var(--columns)) - 1px);    
  -webkit-animation: animatedSoundBars 0ms -800ms linear infinite alternate;    
          animation: animatedSoundBars 0ms -800ms linear infinite alternate;
}

@-webkit-keyframes animatedSoundBars {
  0% {
    top: 0;
  }
  100% {
    top: 98%;
  }
}

@keyframes animatedSoundBars {
  0% {
    top: 0;
  }
  100% {
    top: 98%;
  }
}

.SoundBars-bar:nth-child(1) {
  left: 0;
  -webkit-animation-duration: 680ms;
          animation-duration: 680ms;
}
.SoundBars-bar:nth-child(2) {
  left: calc(100% / 4 * 1);
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
}
.SoundBars-bar:nth-child(3) {
  left: calc(100% / 4 * 2);
  -webkit-animation-duration: 315ms;
          animation-duration: 315ms;
}
.SoundBars-bar:nth-child(4) {
  left: calc(100% / 4 * 3);
  -webkit-animation-duration: 810ms;
          animation-duration: 810ms;
}



