.Timeline {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Timeline-timestamp {
  color: var(--white-50);
  font-size: 0.8em;
}

.Timeline-currentTime {
  left: 0;
  padding-right: 1em;
}

.Timeline-duration {
  right: 0;
  padding-left: 1em;
}

.Timeline-track {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 3px;
  background: var(--white-10);
  border-radius: 3px;
  overflow: hidden;
  position: relative;
}

.Timeline-trackBar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  border-radius: 3px;
  -webkit-transition: width 0.3s linear;
  -o-transition: width 0.3s linear;
  transition: width 0.3s linear;
}

.Timeline-trackBuffered {
  background: var(--white-10);
}

.Timeline-trackCurrentTime {
  background: var(--foreground);
}



