.NavBar {
  height: 3em;
  width: calc(100% - 2em);
  padding: 0 1em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background: var(--menu);
  -webkit-box-shadow: inset 0 -2px 0 var(--black-30);
          box-shadow: inset 0 -2px 0 var(--black-30);
}

.NavBar-section-list {
  display: -ms-flexbox;
  display: flex;
}

.NavBar-section-item {
  padding: 0 1em;
  line-height: 3em;
  border: 0;
  outline: 0;
  background: none;
  color: #fff;
  font-size: 1em;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.NavBar-section-item:hover {
  background: var(--white-05);
  border-bottom-color: var(--white-10);
}

.NavBar-section-item:active {
  border-bottom-color: var(--primary);
}

.NavBar-section-item-selected,
.NavBar-section-item-selected:hover {
  background: var(--white-05);
  color: var(--primary);
  border-bottom-color: var(--primary);
}



