.Modal-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  background: var(--black-50);
  cursor: default;
}

.Modal-window {
  --width: 500px;
  --height: 400px;

  position: fixed;
  width: var(--width);
  height: var(--height);
  top: 50%;
  left: 50%;
  margin-left: calc(var(--width) / -2);
  margin-top: calc(var(--height) / -2);
  border: 5px solid var(--black-50);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  background: var(--background);
}

.Modal-content {
  -ms-flex: 1 1;
      flex: 1 1;
}



