.PlayerItem {
  display: -ms-flexbox;
  display: flex;
  padding: 0.5em;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid var(--white-05);
}

.PlayerItem:hover {
  background: var(--white-05);
}

.PlayerItem-selected {
  color: var(--primary);
}

.PlayerItem-helper {
  opacity: 0.9;
}

.PlayerItem-text {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow: hidden;
}

.PlayerItem-title {
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.PlayerItem-artist {
  color: var(--white-50);
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.PlayerItem-rating {
  width: 40px;
  height: 15px;
  margin-right: 1rem;
}

.PlayerItem-time {
  color: var(--white-50);
}

.PlayerItem-sortHandle {
  margin-left: 0.5em;
}



